.inquiryInput{
    
    margin: 0 auto;
}

.red_span{
    color: red;
    padding-left: 5px;
}

#inquirySection{
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 20px; 
    box-shadow: 0 0 20px lightgray;
}

#inquiryForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
    border-radius: 20px;
}
#inquiryForm>table{
    height: 50vh;
    width: 100%;
}

#inquiryForm>table>tr>td{
    padding: 0;   
}

#inquiryForm>table>tr>td:nth-child(1){
    font-weight: bold;
}

#inquiryForm>table>tr>td>select, #inquiryForm>table>tr>td>select>option, #inquiryForm>table>tr>td>textarea{
    border: none;
    border: solid 1px lightgray;
    border-radius: 5px;
    margin: 0;

}

#inquiryForm>table>tr>td>input{
    border: none;
    border: solid 1px lightgray;
    border-radius: 5px;
    margin: 0;
    text-align: center;
}

#inquiryForm>table>tr>td>input:focus{
    outline: none;
    text-align: center;
}

#inquiryForm>table>tr>td>select:focus,
#inquiryForm>table>tr>td>textarea:focus{
    outline: none;
    
}

#inquiryForm>table>tr>td>input::placeholder{
    text-align: center;
    font-style: italic;
}


#inputName{
    width: 160px;
    height: 30px;
}

#emailId, #emailDomain{
    width: 160px;
    height: 30px;
}
#selectDomain{
    width: 150px;
    height: 30px;
}

#inputNumber1, #inputNumber2, #inputNumber3{
    width: 120px;
    height: 30px;
}

#inputAsk{
    width: 750px;
    height: 100%;
    resize: none;
}

#localAddress{
    width: 170px;
    height: 30px;
}

#inquiry_Btn{
    width: 150px;
    height: 40px;
    border: none;
    color: white;
    border-radius: 20px;
    background-color: #a6beeb;
    font-size: 20px;
    margin-top: 20px;
    box-shadow: 0 0 5px lightgray;
    cursor: pointer;
}

#inquiry_Btn:hover{
    background-color: #93A9D1;
}

#inputCheckbox{
    margin: 10px;
}

@media (max-width:820px){
    #inquiryForm{
        height: 350px;
        width: 300px;
    }
    #inquiryForm>table{
        height: 250px;
        width: 300px;
    }
    #inquiryForm>table>tr>td{
        font-size: 15px;  
        padding: 0;
        /* outline: solid 1px black;   */
    }
    #inputName{
        width: 70px;
        height: 20px;
    }
    
    #emailId, #emailDomain{
        width: 70px;
        height: 20px;
    }
    #selectDomain{
        width: 50px;
        height: 20px;
        display: none;
    }
    
    #inputNumber1, #inputNumber2, #inputNumber3{
        width: 40px;
        height: 20px;
    }
    
    #inputAsk{
        width: 290px;
        height: 50px;
        resize: none;
    }
    
    #localAddress{
        width: 170px;
        height: 30px;
    }
    
    #inquiry_Btn{
        width: 100px;
        height: 25px;
        border: none;
        color: white;
        border-radius: 20px;
        background-color: #a6beeb;
        font-size: 15px;
        margin-top: 20px;
        box-shadow: 0 0 5px lightgray;
        cursor: pointer;
    }
    
    #inquiry_Btn:hover{
        background-color: #93A9D1;
    }
    
    #inputCheckbox{
        margin: 10px;
    }
    
}